import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { motion } from "framer-motion";
import { addNote } from "../reducers/notesSlice";
import { allNotes } from "../reducers/notesSlice";

import BackArrow from "../images/arrow_back_black_48dp.svg";
import saveIcon from "../images/save_black_48dp.svg";

const Note = () => {
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");

  const notes = useSelector(allNotes);
  const dispatch = useDispatch();
  const history = useNavigate();

  const handleSave = (e) => {
    e.preventDefault();
    if (title == "" && content == "") {
      history("/Notes-PWA");
      return;
    }
    setTimeout(() => {
      dispatch(addNote(title, content));
      localStorage.setItem("Notes", JSON.stringify(notes));
      history("/Notes-PWA");
    }, 30);
  };

  return (
    <motion.div
      className="Note"
      // initial={{ scale: 0, opacity: 0 }}
      // animate={{ scale: 1, opacity: 1 }}
      initial={{ y: "100vw", opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 0.3, type: "spring" }}
    >
      <form>
        <label htmlFor="title"></label>
        <input
          type="text"
          value={title}
          placeholder="Title"
          onChange={(e) => setTitle(e.target.value)}
        />
        <label htmlFor="content"></label>
        <textarea
          id="text-content"
          value={content}
          placeholder="Content"
          onChange={(e) => {
            setContent(e.target.value);
          }}
        />
      </form>
      <img
        onClick={handleSave}
        className="backButton"
        src={!title && !content ? BackArrow : saveIcon}
      />
    </motion.div>
  );
};

export default Note;
