import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { useParams, useNavigate } from "react-router-dom";
import { allNotes, addNote, deleteNote } from "../reducers/notesSlice";
import { useState } from "react";
import { motion } from "framer-motion";

import BackArrow from "../images/arrow_back_black_48dp.svg";
import saveIcon from "../images/save_black_48dp.svg";

const EditNote = () => {
  const { id } = useParams();
  const notes = useSelector(allNotes);
  let note;
  notes.forEach((noteItem) => {
    if (noteItem.id === id) {
      note = noteItem;
      console.log(noteItem.title);
      console.log(noteItem.content);
    }
  });

  const [title, setTitle] = useState(note.title);
  const [content, setContent] = useState(note.content);

  const dispatch = useDispatch();
  const history = useNavigate();

  const handleSave = (e) => {
    // e.preventDefault();
    if (!title && !content) {
      dispatch(deleteNote(id));
      history("/Notes-PWA");
      return;
    }

    // let text = document.getElementById("text-content");
    // console.log(text);

    setTimeout(() => {
      dispatch(deleteNote(id));
      dispatch(addNote(title, content));
      localStorage.setItem("Notes", JSON.stringify(notes));
      history("/Notes-PWA");
    }, 30);
  };

  return (
    <motion.div
      className="Note"
      // initial={{ scale: 0, opacity: 0 }}
      // animate={{ scale: 1, opacity: 1 }}
      // exit={{ opacity: 0 }}

      initial={{ scale: 2, opacity: 0 }}
      animate={{ scale: 1, opacity: 1 }}
      transition={{ duration: 0.2 }}
    >
      <form>
        <label htmlFor={title}></label>
        <input
          type="text"
          value={title}
          placeholder="Title"
          onChange={(e) => setTitle(e.target.value)}
        />
        <label htmlFor="content"></label>
        <textarea
          value={content}
          placeholder="Content"
          onChange={(e) => {
            setContent(e.target.value);
          }}
        />
      </form>
      <br />
      <img
        onClick={handleSave}
        className="backButton"
        src={!title && !content ? BackArrow : saveIcon}
      />
    </motion.div>
  );
};

export default EditNote;
